
.u-sort {
  position: absolute;
  width: 7px;
  height: 28px;
  color: #999;
  cursor: pointer;
}
.u-sort .u-sort-up {
  position: absolute;
  top: 4px;
}
.u-sort .u-sort-down {
  position: absolute;
  top: 10px;
}
.u-sort.up .u-sort-up {
  color: black;
}
.u-sort.down .u-sort-down {
  color: black;
}



